.intro {
    height: 100vh;
    width: 100%;
    background: transparent;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;

    &__content {
        position: relative;
        z-index: 2;
        
    }
    &-slider__content-slider{
        width: 100%;
        height: 100vh;
        margin: 0;
    }
    &-slider__content{
        width: 100%;
        height: 100vh;
        margin-top: auto;
    }
    &-slider__wrapper{
        display: flex;
        flex-direction: column;
        width: 100%;
        height: 100%;
    }
    
}

.swiper-scrollbar{
    display: block !important;
    position: absolute;
    top: 0;
    right: 90px;
    width: 1px;
    height: 100%;
    background-color: hsla(0,0%,100%,.3);
    z-index: 410;
}
.swiper-drag-scrollbar{
    position: absolute;
    top:0;
    left: -1;
    width: 3px;
    background-color: #fff;
    cursor: pointer;
}
.swiper-scrollbar-lock {
    display: block;
}
.swiper-drag-scrollbar{
    height: calc(100% / 6) !important;
}

